header{
    height: 100vh;
    padding-top: 6rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.header_socials::after{
    content: '';
    width: 2px;
    height: 2rem;
    background: var(--color-primary);
}

.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}
.scroll_down{
    position:absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
#dd{
    color: rgba(180, 135, 135, 1);
}
#ddd{
    color: wheat;
}

#msalaa{
    height: 100%;
    width: 20rem;
    position: relative;
    left: 20%;
}
#logo{
    animation: fill 0.5s ease forwards 4.2s;
}
#logo path:nth-child(2){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 0.5s ease forwards;
}#logo path:nth-child(3){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 0.7s ease forwards 0.3s;
}#logo path:nth-child(4){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 0.9s ease forwards 0.6s;
}#logo path:nth-child(5){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 1.1s ease forwards 0.9s;
}#logo path:nth-child(6){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 1.3s ease forwards 1.2s;
}#logo path:nth-child(7){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 1.5s ease forwards 1.5s;
}#logo path:nth-child(8){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 1.7s ease forwards 1.8s;
}#logo path:nth-child(9){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 1.9s ease forwards 2.1s;
}#logo path:nth-child(10){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 2s ease forwards 2.4s;
}
#logo path:nth-child(11){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 2.1s ease forwards 2.7s;
}#logo path:nth-child(12){
    stroke-dasharray: 172px;
    stroke-dashoffset: 172px;
    animation: anim 2.3s ease forwards 3s;
}#logo path:nth-child(13){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 2.5s ease forwards 3.3s;
}#logo path:nth-child(14){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 2.7s ease forwards 3.6s;
}#logo path:nth-child(15){
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: anim 2.9s ease forwards 3.9s;
}


@keyframes anim {
    to{
        stroke-dashoffset: 0;

    }
}
@keyframes fill {
    from{
        fill: transparent;
    }
    to{
        fill: white;
    }
}



/* =================== TABLET ============ */
@media screen and (max-width: 1024px) {
    header{
        height: 68vh;
    }
}

/* =================== Phone ============ */
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    .header_socials, .scroll_down{
        display: none;
    }
    #logo{
        width: 81%;
    }
    .me{
        width: 18rem;
        height: rem;
    }
    header{
        padding: 15px;
        height: 100vh;
        position: relative;
        left: 8%;
    }
    [data-parallax-id="header"] figure {
        transform: none !important;
        top: 0 !important;
        bottom: 0 !important;
        will-change: auto !important;
        }
}