footer{
    background: rgb(35, 111, 161);
    padding: 2rem 0;
    text-align: center;
    font-size: 0.9rem;  margin-top: 7rem;
    border-radius: 2rem 0 15rem 0 ;
}
footer a {
    color: var(--color-bg);
}
.footer_logo{
    color: rgb(46, 43, 43);
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permlinks{
    font-size: 1.3rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer_socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 5rem;
    position: relative;
    bottom: 25px;
    left:41%;

}
.footer_socials a {
    background: rgb(56, 64, 99);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer_socials a :hover{
    background:white;
    color: var(--color-bg);
    border-color: var(--color-bg);
    transition: var(--transition);
}
ul >li a{
    color:rgb(7, 29, 88) ;
}


/* =================== Phone ============ */
@media screen and (max-width: 600px) {
    .permlinks{
        flex-direction:column ;
        gap: 1.5rem;
    }
    .footer_socials{
        margin-bottom: 2.6rem;
    }
    footer{
        position: relative;
        left: 8%;
    }
    .footer_socials{
        position: relative;
        bottom: 40px;
        left: 1%;
    }

}