.experience_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;

}
.web_dev{
  position: relative;
  right: 20%;
}
.others{
  position: relative;
  right: 15%;
}




.experience_container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.experience_container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.experience_container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience_content{
     display: grid;
     grid-template-columns: 1fr 1fr;
     row-gap: 2rem;
}
.experience_details {
    display: flex;
    gap: 1rem;

}
.experience_details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}
.others{
    width: 40rem;
}
.web_dev{
    width: 40rem;
}

/* =================== TABLET ============ */
@media screen and (max-width: 1024px) {
    .experience_container{
        grid-template-columns: 1fr;
    }
    .experience_container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience_content{
        padding: 1rem;
    }
}

/* =================== Phone ============ */
@media screen and (max-width: 600px) {
    .experience_container{
      
        gap: 2rem;
    }
    .experience_container > div{
      position: relative;
      left: 8%;
      width: 100%;
      padding: 2rem 1rem;
    }
    #head{
      position: relative;
      left: 8%; 
    }
}


/* ================ Bouncing Letters ===================*/

h2 span{
    display: inline-block;
    animation: animat 4s linear infinite;
}
@keyframes animat {
    0%{
        transform: translateY(0px);
    }
    10%{
        transform: translateY(-30px);
    }
    15%{
        transform: translateY(0px);
    }
    25%{
        transform: translateY(-10px);
    }
    30%{
        transform: translateY(0px);
    }
    100%{
        transform: translateY(0px);
    }

  }


  .h2 span:nth-child(2) {
    animation-delay: 0s;
  }
  .h2 span:nth-child(3) {
    animation-delay: 0.2s;
  }
  .h2 span:nth-child(4) {
    animation-delay: 0.4s;
  }
  .h2 span:nth-child(5) {
    animation-delay: 0.6s;
  }
  .h2 span:nth-child(6) {
    animation-delay: 0.8s;
  }
  .h2 span:nth-child(7) {
    animation-delay: 1.8s;
  }
  .h2 span:nth-child(8) {
    animation-delay: 2s;
  }
  .h2 span:nth-child(9) {
    animation-delay: 2.2s;
  }
  .h2 span:nth-child(10) {
    animation-delay: 2.4s;
  }
  .h2 span:nth-child(11) {
    animation-delay: 2.6s;
  }
  .h2 span:nth-child(12) {
    animation-delay: 2.8s;
  }
  .h2 span:nth-child(13) {
    animation-delay: 3s;
  }
  .h2 span:nth-child(14) {
    animation-delay: 3.2s;
  }
  .h2 span:nth-child(15) {
    animation-delay: 3.4s;
  }
  .h2 span:nth-child(16) {
    animation-delay: 03.6s;
  }
  .h2 span:nth-child(17) {
    animation-delay: 3.8s;
  }
  .h2 span:nth-child(18) {
    animation-delay: 4s;
  }
  .h2 span:nth-child(19) {
