.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary, transparent));
    display: grid;
    place-items: center;
}
.about_me-img{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about_me-img:hover{
    transform: rotate(0);
}

.about_cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 6rem;
}
.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    width: 140%;
    margin-right: 30px;
    text-align: center;
    transition: var(--transition);
}
.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about_card h5{
    font-size: 0.95rem;
}
.about_card small{
    font-size: 0.8rem;
    color: var(--color-light);
}
.about_content p{
    margin: 2rem 0 3rem;
    color: rgb(204, 159, 247);
}
#msala{
    height: 48vh;
    width: 48vh;
}



/* =================== TABLET ============ */
@media screen and (max-width: 1024px) {
    .about_container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content p {
        margin: 1rem 0 0.1rem;
    }
    .about_card{
        width: 10rem;
    }
    .about_cards{
        gap: 13rem;
    }
}


/* =================== Phone ============ */
@media screen and (max-width: 600px) {

    .about_me{
        width: 65%;
        margin: 0 auto 3rem;
        
    }
    .about_cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        
    }
    .about_card{
        width: 10rem;
    }
    .about_content {
        text-align: center;
    }
    .about_content p {
        margin: 1.5rem 0 ;
    }
}

/* ========================== bouncing letterss ================ */

h2 span{
    display: inline-block;
    animation: animate 4s linear infinite;
}
@keyframes animate {
    0%{
        transform: translateY(0px);
    }
    10%{
        transform: translateY(-30px);
    }
    15%{
        transform: translateY(0px);
    }
    25%{
        transform: translateY(-10px);
    }
    30%{
        transform: translateY(0px);
    }
    100%{
        transform: translateY(0px);
    }

  }


  h2 span:nth-child(2) {
    animation-delay: 0.1s;
  }
  h2 span:nth-child(3) {
    animation-delay: 0.2s;
  }
  h2 span:nth-child(4) {
    animation-delay: 0.4s;
  }
  h2 span:nth-child(5) {
    animation-delay: 0.6s;
  }
  h2 span:nth-child(6) {
    animation-delay: 1.8s;
  }
  h2 span:nth-child(7) {
    animation-delay: 1.8s;
  }
