*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

.imgs > img{
    height: 50vh;
    width: 600px;
    margin-left: 50px;
    border-radius: 10px;
}

.imgs{
    display: grid;
    grid-template-columns: 2fr 2fr;
    gap: 2em;
}
.img{
    display: grid;
    grid-template-columns: 2fr 2fr;
    gap: 2em;
}
#p{
    text-align: center;
    margin-top: 110px;
    color: rgb(122, 194, 126);
}
#hh{
    padding: 10px;
    margin: 20px;
    color: var(--color-primary);
    position: relative;
    left: 20%;
}